@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Playfair Display', serif; */



/*---------- Navbar CSS ---------------*/
.navbar{
  font-family: 'Poppins', sans-serif;
  font-style: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: 0px solid gray !important;
}

.nav-link{
  font-weight: 300 !important;
}

.active{
  border-bottom: 2px solid #000;
}

.navbar-brand{
  font-weight: 700px;
  font-family: 'roboto', sans-serif;
  letter-spacing: 3px;
}

.search-form {
  margin-right: 10px;
  margin-top: 4px;
}
.search-form input {
  border: 1px solid;
  border-radius: 10px;
}
.search-form button {
  background-color: #76d5bd;
  border-radius: 0;
  color: white;
  border-radius: 10px;
}


/*------------- Footer CSS ------------*/
.footer{
  color: rgb(83, 173, 142);
  padding: 25px;
  background: -webkit-linear-gradient(to right,#434343,#000000);
  background: linear-gradient(to right, #434343, #000000);
}

.footer a{
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover{
  color: #dcc67e;
  border-bottom: 1px solid #7c6108;
  font-size: 18px;
}


/*--------------- PageNot Found CSS ------------*/
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}


/*----------- About Contact Privacy pages CSS --------------*/
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}


